import React from "react";

export default function User({ user, onHide }) {
  const [showAddress, setShowAddress] = React.useState(false);
  return (
    <div className="App">
      <img src={user.profilePicture} height="200px" alt="John Smith Profile" />
      <h1>
        {user.firstName} {user.lastName}
      </h1>
      <p>
        <small>Phone: {user.phone}</small> <br />
        <small>
          Email: <a href={`mailto:${user.email}`}>{user.email}</a>
        </small>{" "}
        <br />
        <br />
        <a href={user.website}>Website</a>
      </p>
      {showAddress && (
        <p>
          {user.address.street} <br />
          {user.address.zipcode} {user.address.city} <br />
          {user.address.country} <br />
        </p>
      )}
      <button type="button" onClick={() => setShowAddress(!showAddress)}>
        {showAddress ? "Hide" : "Show"} Address
      </button>
    </div>
  );
}
